<template>
    <div>
        <div class="body">
            <div class="tags">
                <div class="tag" :class="selectedTag === tag ? 'selected' : ''" v-for="tag in Object.keys(tags)" :key="tag"
                    @click="updateTag(tag)">{{ tag }}&nbsp;&nbsp;{{ tags[tag] }}</div>
            </div>
            <div class="blogs">
                <ul class="blog-list">
                    <li class="blog-item" v-for="blog in currentBlogs" :key="blog.fileName" @click="navToDetailPage(blog)">
                        <div class="blog-date">{{ blog.createDate }}</div>
                        <div class="blog-title">{{ blog.title }}</div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
import { useBlogStore } from '../store/blogstore.js'
import { storeToRefs } from 'pinia'
const store = useBlogStore()

export default {
    data() {
        return {
            blogs: [],
            tags: {},
            selectedTag: "Kotlin"
        }
    },

    watch: {
        '$route': "refreshList"
    },

    computed: {
        currentBlogs() {
            return this.getBlogsByTag(this.selectedTag)
        }
    },

    activated() {
        const { blogList, tags } = storeToRefs(store)
        this.tags = tags
        this.blogs = blogList
        console.log("tags", tags)
        this.$route.query.tag ? this.selectedTag = this.$route.query.tag : this.selectedTag = "Android"
    },

    methods: {
        navToDetailPage(blog) {
            this.$router.push({
                name: 'detail',
                //传递blog参数到detail页面
                query: {
                    fileName: blog.fileName
                }
            })
        },
        updateTag(tag) {
            this.selectedTag = tag
        },

        refreshList() {
            if (this.selectedTag != this.$route.query.tag) {
                this.updateTag(this.$route.query.tag)
            }
        },

        getBlogsByTag(tag) {
            return this.blogs.filter((blog) => {
                if (blog.tags) {
                    return blog.tags.includes(tag)
                } else {
                    return false
                }
            });
        },
    }
}
</script>

